<template>
  <div class="header-container">
    <div class="film-preview">
      <!-- 大封面 -->
      <img :src="flag && filmData.poster" alt="">
    </div>
    <div class="film-content">
      <div class="film-info clearfix">
        <div class="film-info-pic l">
          <!-- 电影封面 -->
          <img :src="flag && filmData.cover" alt="" />
        </div>
        <div class="film-info-right r">
          <div class="film-info-right-text">
            <!-- 电影名 -->
            <h2 class="film-name">{{flag && filmData.name}}</h2>
            <p>
              <!-- 电影相关 -->
              <span class="film-time">{{flag && filmData.duration}}</span> /
              <span class="film-type">{{flag && filmData.type}}</span> /
              <span class="film-place">中国大陆</span>
            </p>
            <p>
              <span class="film-show">{{flag && filmData.releasedate}}</span
              ><span class="film-place">在中国大陆上映</span>
            </p>
          </div>
          <div class="film-info-right-choice clearfix">
            <div class="wanna btn l">
              <span class="iconfont icon-aixin"></span>
              <em>想看</em>
            </div>
            <div class="seen btn r">
              <span class="iconfont icon-star"></span>
              <em>看过</em>
            </div>
          </div>
        </div>
      </div>
      <div class="film-mouth">
        <div class="mouth-title clearfix">
          <div class="mouth-kb-img l">
            <img src="../assets/img/kb.png" alt="">
            <span class="iconfont icon-gantanhao"></span>
          </div>
          <span class="iconfont icon-youjiantou r"></span>
        </div>
        <div class="mouth-main clearfix">
          <div class="mouth-grade l">
            <p class="film-grade fw">{{flag && filmData.score}}</p>
              <van-rate v-model="value" allow-half void-icon="star" void-color="#eee" color="#ff7b00" :readonly="true" class="film-star"/>
            <p class="film-ev fw">{{flag && filmData.number}}万人评价</p>
          </div>
          <div class="mouth-starlevel l">
            <div class="starlevel5 starlevel">
              <img src="../assets/img/auj5.png" alt="">
              <span class="ganggang"><i :style="{'width':gang_width+'%'}"></i></span>
            </div>
            <div class="starlevel4 starlevel">
              <img src="../assets/img/auy4.png" alt="">
              <span class="ganggang"><i :style="{'width':gang_width/1.25+'%'}"></i></span>
            </div>
            <div class="starlevel3 starlevel">
              <img src="../assets/img/av3.png" alt="">
              <span class="ganggang"><i :style="{'width':gang_width/2+'%'}"></i></span>
            </div>
            <div class="starlevel2 starlevel">
              <img src="../assets/img/av2.png" alt="">
              <span class="ganggang"><i :style="{'width':gang_width/10+'%'}"></i></span>
            </div>
            <div class="starlevel1 starlevel">
              <img src="../assets/img/av1.png" alt="">
              <span class="ganggang"><i :style="{'width':gang_width/20+'%'}"></i></span>
            </div>
          </div>
          <div class="mouth-info r">
            <div class="mouth-info-top margint">
              <span class="fw">11.3万</span><i>人想看</i>
            </div>
            <div class="mouth-info-mid margint">
              <span class="fw">128.9万</span><i>人看过</i>
            </div>
            <div class="mouth-info-bot margint">
              <span class="fw">74%</span><i>大V推荐度<em class="iconfont icon-xinlang"></em></i>
            </div>
          </div>
        </div>
        <div class="mouth-rank clearfix">
          <div class="mouth-rank-left l">
            <img src="../assets/img/jp.png" alt="" class="jp">
            <span>周票房榜第<i class="rank">1</i>名</span>
          </div>
          <div class="mouth-rank-right r">
            <span class="l">累计票房4.3亿</span>
            <span class="iconfont icon-youjiantou l"></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Rate } from 'vant';
export default {
  name: "filmDetailHeader",
  components: {
    [Rate.name]:Rate
  },
  props:["headerData"],
  data() {
    return {
      value:0,
      gang_width:10,
      Mid:1,
      filmData:null,
      flag:false,
    };
  },
  mounted(){
    let timer = setInterval(()=>{
      this.gang_width += 1;
      if(this.gang_width >= 100){
        clearInterval(timer);
      }
    },10)
  },
  created(){
    this.Mid = this.$store.state.selectMid;
    this.$axios.get(`/film/findById?id=${this.Mid}`).then(res=>{
      console.log(res.data);
      this.filmData = res.data;
      this.value = this.filmData.score/2;

      this.flag = true;
      console.log(this.value);
    });
  }
};
</script>
<style scoped>
