<template>
  <div class="previewedVideo">
    <van-swipe style="height: 100%" vertical @change="fun" :show-indicators="false">
      <van-swipe-item v-for="(item,index) in video" :key="index">
         <div class="video-item">
            <video :src="item.src" :autoplay="item.isShow" loop ref="Video"></video>
         </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from "vant";

export default {
  name: "previewedVideo",
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  data() {
    return {
       video:[
          {src:"/video/video3.mp4",isShow:true},
          {src:"/video/video4.mp4",isShow:false},
          {src:"/video/video5.mp4",isShow:false},
          {src:"/video/video6.mp4",isShow:false},
          {src:"/video/video7.mp4",isShow:false},
          {src:"/video/video8.mp4",isShow:false},
          ]
    };
  },
  methods: {
     fun(index){
      this.$refs.Video.forEach((item,i)=>{
         if(i == index){
            item.play();
         }else{
            item.pause();
            item.currentTime = 0;
         }
      })

     }
  },
};
</script>
<style scoped>
.previewedVideo{
   height: calc(100% - 0.5rem); 
}
.video-item{
   height: 100%;
   display: flex;
   align-items: center;
}
video{
   width: 100%;
   height: 100%;
}
</style>