<template>
  <div class="chooseSeat">
    <chooseSeatHeader v-if="true" />
    <chooseSeatMain
      @primarySeat="getpriSeat"
      :priSeat="pSeat"
      :selected="selected"
      :disabled="disabled"
      v-if="true" 
      ref="searching"
    />
    <chooseSeatBottom :priSeat="pSeat" @remPri="getpriSeat" v-if="true" @toOrder="tosearch"/>
  </div>
</template>

<script>
import chooseSeatHeader from "../components/chooseSeatHeader";
import chooseSeatMain from "../components/chooseSeatMain";
import chooseSeatBottom from "../components/chooseSeatBottom";
import { Toast } from "vant";
export default {
  name: "chooseSeat",
  components: {
    chooseSeatHeader,
    chooseSeatMain,
    chooseSeatBottom,
    [Toast.name]: Toast,
  },
  data() {
    return {
      disabled:false,
      pSeat: null,
      selected: [],
    };
  },
  methods: {
    getpriSeat(seat) {
      this.pSeat = seat;
    },
    tosearch(){
      this.$refs.searching.searching();
    }
  },
  created() {
    // Toast.loading({
    //   message: "加载中...",
    //   forbidClick: true,
    //   overlay:true,
    //   duration:5000
    // });
    this.$axios({

      method: "get",

      url: `/seat/findallseat?fid=${this.$store.state.selectMid}&tid=1&cid=${this.$store.state.selectCid}`,

    }).then((res) => {
      // Toast.clear({
      //     clearAll: true
      // })
      console.log("已选座位",res.data);
      this.selected = res.data;
      // this.disabled = true;
    }).catch(()=>{
        Toast.fail({
            message:"请求超时",
            duration:3000
        })
    });
  },
};
</script>
<style scoped>
.chooseSeat {
  background: #f5f5f5;
  height: 100%;
}
</style>