<template>
<div>
  <div class="controlBG" :style="{ opacity: opac }"></div>
  <div class="film-detail-top clearfix">
      <span class="iconfont icon-zuojiantou" @click="goback"></span>
      <h4 :style="{ opacity: opac }">
        <span class="iconfont icon-bofang"></span>
        <i>播放精彩预告片</i>
      </h4>
      <span class="transmit iconfont icon-zhuanfa"></span>
  </div>
</div>
</template>

<script>
export default {
  name: "flimDetailTop",
  components: {},
  data() {
    return {
      top: null,
      opac: 0,
      oldtop:0
    };
  },
  methods: {
    handleScroll() {
    //获取滚动条高度
      this.top = Math.floor(document.body.scrollTop ||document.documentElement.scrollTop || window.pageYOffset);
      if(this.oldtop<this.top){
        this.oldtop = this.top;
        this.opac += this.top / 6000;
      }
      if(this.oldtop>this.top){
        this.oldtop = this.top;
        this.opac -= this.top / 8000;
        if(this.top == 0){
            this.opac = 0;
        }
      }
    },
    goback() {
      if (!this.$store.state.userPath.length) {
        this.$router.push({ path: "/" });
      } else {
        let path = this.$store.state.userPath;
        this.$router.push({ path: path[path.length - 1] });
        this.$store.commit("removePath");

        console.log("用户路径", this.$store.state.userPath);
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll, false);
  },
};
</script>
<style scoped>
.film-detail-top {
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  box-sizing: border-box;
  padding: 0.1rem 0.12rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.controlBG{
  background: #626169;
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 0.39rem;
}
.icon-zuojiantou {
  font-size: 0.16rem;
}
.icon-bofang {
  font-size: 0.16rem;
  margin-right: 0.04rem;
  vertical-align: middle;
}
.film-detail-top h4 {
  font-size: 0.12rem;
  width: 100%;
  height: 100%;
  text-align: center;
}
.film-detail-top h4 i {
  padding-top: 0.8rem;
}
.icon-zhuanfa {
  font-size: 0.14rem;
}
</style>