//创建vue-router对象
import Vue from "vue";
import VueRouter from "vue-router";
import store from '../store';

import Home from "../pages/Home.vue";

import Show from "../pages/Show.vue";

import FastLooking from "../pages/FastLooking.vue";

import CinemaList from "../pages/CinemaList.vue";

import MovieListPage from "../pages/MovieListPage.vue";

import filmDetail from "../pages/filmDetail.vue";

import CinemaItem from "../pages/CinemaItem.vue";

import chooseSeat from "../pages/chooseSeat.vue";

import OrderPage from "../pages/OrderPage.vue";

import pay from "../pages/pay.vue";



import Mine from "../pages/Mine.vue";
import Singer from "../pages/Singer.vue";
import login from "../pages/login.vue";

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}




Vue.use(VueRouter);
//路由配置
let routes = [
        { path: "/", component: Home },
        { path: "/Home", component: Home },
        { path: "/Show", component: Show },
        { path: "/FastLooking", component: FastLooking },
        { path: "/MovieListPage", component: MovieListPage },
        { path: "/filmDetail", component: filmDetail ,name:"filmDetail"},
        { path: "/CinemaList", component: CinemaList ,name:"CinemaList"},
        { path: "/CinemaItem", component: CinemaItem ,name:"CinemaItem"},
        { path: "/chooseSeat", component: chooseSeat ,name:"chooseSeat"},
        { path: "/OrderPage", component: OrderPage ,name:"OrderPage"},
        
        { path: "/pay", component: pay},


        { path: "/Mine", component: Mine},
        { path: "/Singer", component: Singer},
        { path: "/login", component: login},


    ]
    //创建vue-router对象
let router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    store.commit("updateMidPath",from.path);
    store.commit("updateUserPath",from.path);
    if (to.path == "/chooseSeat") {
        if (localStorage.getItem("userTel")) {
            next();
        } else {
            next({ path: "/login", query: { fromPath: from.path } })
        }
    };
    next();
});
export default router;