<template>
  <div>
   <MovieListSearch />
    <van-tabs :active="active" sticky @click="saveAc">
      <van-tab title="热映">
        <BoxOffice />
        <FilmsList />
      </van-tab>
      <van-tab title="影院">
        <CinemaList />
      </van-tab>
      <van-tab title="即将上映" disabled>内容 3</van-tab>
      <van-tab title="在线看" disabled>内容 4</van-tab>
    </van-tabs>
    <Bottom />

  </div>
</template>

<script>
import FilmsList from "@/components/FilmsList";
import BoxOffice from "@/components/BoxOffice";
import MovieListSearch from "@/components/MovieListSearch";

import CinemaList from "@/components/CinemaList";
import Bottom from "@/components/Bottom";

export default {
  name: "MovieListPage",
  data() {
    return {
      active: null,
    };
  },
  components: {
    FilmsList,
    BoxOffice,
    MovieListSearch,
    Bottom,
    CinemaList
  },
  created(){
    this.active = this.$store.state.active;
  },
  methods:{
   saveAc(){
     this.$store.commit("updateActive",this.active);
   }
  }
};
</script>

