<script>
import { Tab, Tabs } from "vant";
import { Rate } from "vant";
import { Swipe, SwipeItem } from "vant";
import { Button } from "vant";
export default {
  name: "flimDetailMain",
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Button.name]: Button,
    [Rate.name]: Rate,
  },
  data() {
    return {
      flag:false,
      filmData:[],
      // active:false,
      casts: [],
      Mid:0,
      review: ["全部","最新","好评8275","差评168","影评达人97","有图98","购票8858"],
      reviews:[]
    };
  },
  created() {
    this.Mid = this.$store.state.selectMid;
    this.getMain();
  },
  methods:{
    getMain(){
      this.$axios.all([this.$axios({url:`/film/findById?id=${this.Mid}`}),this.$axios({url:`/actor/findById?id=${this.Mid}`}),this.$axios({url:`/review/findByFid?fid=${this.Mid}`})]).then(
        this.$axios.spread((films,actors,review)=>{
          this.filmData = films.data;
          this.casts = actors.data;
          this.reviews = review.data;
          // console.log(this.casts);
          this.flag = true;
        })
      );
    },
    tocinema(){
      this.$router.push({path:"/CinemaList"});
    }
  }
};
</script>
