<template>
  <div class="chooseSeatMain">
    <table class="seat">
      <tr v-for="x in 8" :key="x">
        <td
          v-for="y in 13"
          :class="{ can: true }"
          :key="y"
          :ref="x"
          @click="choose(x, y)"
        ></td>
      </tr>
      <!-- <div class="best-viewing"></div> -->
      <div class="row-number">
        <ul>
          <li>1</li>
          <li>2</li>
          <li>3</li>
          <li>4</li>
          <li>5</li>
          <li>6</li>
          <li>7</li>
          <li>8</li>
        </ul>
      </div>
    </table>
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {
  name: "chooseSeatMain",
  components: {
      [Toast.name]:Toast
  },
  data() {
    return {
      primary: [],
      status: [{ can: true, cant: false, check: false }],
      chooseSeat: [],
    };
  },

  props: ["priSeat","selected","disabled"],

  methods: {
    //处理父组件传入的值，更新页面
    updateSeat() {
      if (this.priSeat.length) {
        // for(let item in this.$refs){
        //   for(let j=1;j<=13;j++){
        //     this.priSeat.forEach(idx=>{
        //       if(idx.x == item && idx.y == j){
        //         this.$refs[item][j-1].classList.add("check");
        //       }
        //       // else{
        //       //   this.$refs[item][j-1].classList.remove("check");
        //       // }
        //     })
        //   }       
        // }
        this.priSeat.forEach(item =>{
          this.$refs[item.x][item.y-1].classList.add("check");
        })
      }else{
        for(let i in this.$refs){
          for(let j=0;j<12;j++){
            this.$refs[i][j].classList.remove("check")
          }
        }
      }
    },
    // 点击时
    choose(x, y) {
      if (this.$refs[x][y - 1].getAttribute("class").indexOf("cant") != -1) {
        Toast("哎呀，这个座位已经被别人选了");
        return;
      }
      if (this.$refs[x][y-1].getAttribute("class").indexOf("check") == -1) {
        let pSeat = {};
        this.$refs[x][y-1].classList.add("check");
        //向父组件传值
        pSeat.x = x;
        pSeat.y = y;

        this.primary.push(pSeat);
        this.$emit("primarySeat", this.primary);
      } else {
        this.$refs[x][y - 1].classList.remove("check");
        this.$refs[x][y - 1].classList.add("can");
        this.primary.forEach((item, index) => {
          if (item.x == x && item.y == y) {
            this.primary.splice(index, 1);
          }
        });
      }
    },

    // 已选
    checked() {
      this.selected.forEach((item) => {
        this.$refs[item.x][item.y-1].setAttribute("class", "cant");
      });
    },
    //提交时检索哪些被选中
    searching() {
      this.chooseSeat = [];
      for (let Xindex in this.$refs) {
        this.$refs[Xindex].forEach((item, Yindex) => {
          if (item.getAttribute("class").indexOf("check") != -1) {
            let seat = {};
            seat.x = Number(Xindex);
            seat.y = Yindex + 1;
            this.chooseSeat.push(seat);
          }
        });
      }
      this.$store.commit("updateSelectSeat",this.chooseSeat);
    },
    //返回时遍历已选中
    goBackchecked(){
      let arr = [];
      if(this.$store.state.selectSeat){
        this.$store.state.selectSeat.forEach(item => {
          this.$refs[item.x][item.y-1].setAttribute("class", "check");
        });
      }
      this.$emit("primarySeat", this.$store.state.selectSeat);
    }
  },
  mounted(){
    this.checked();
    this.goBackchecked();
  },
  // mounted() {
  //   this.checked();
  // },
  watch: {
    priSeat:{
      deep:true,
      handler:function(newv) {
          this.updateSeat();
      }
    },
    selected:{
      deep:true,
      handler:function(newval){
          this.checked();
      }
    }
  }
};
</script>
<style scoped>
.seat {
  padding: 0 0.58rem;
  width: 100%;
  position: relative;
}
.seat tr {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 0.04rem;
}
.seat td {
  width: 0.14rem;
  height: 0.15rem;
  background-size: 100% 100%;
}
.can {
  background: url("../assets/img/kexuan.png");
}
.cant {
  background: url("../assets/img/at6.png");
}
.check {
  background: url("../assets/img/yixuan.png");
}
.best-viewing {
  width: 0.8rem;
  height: 0.62rem;
  border: 1px dashed #da4064;
  border-radius: 0.04rem;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -0.4rem;
  margin-top: -0.24rem;
}
.row-number {
  width: 0.11rem;
  font-size: 0.06rem;
  color: #fff;
  background: rgba(196, 196, 196, 0.7);
  text-align: center;
  border-radius: 0.1rem;
  position: absolute;
  left: 0;
  top: -0.05rem;
}
.row-number li {
  padding-top: 0.1rem;
  padding-bottom: 0.04rem;
}
</style>