<template>
<div id="choose-seat-box">
  <div class="choose-seat-top">
      <span class="iconfont icon-zuojiantou" @click="goback"></span>
      <h4 class="cinema-name">{{cinema.cinemaName}}</h4>
      <div class="choose-seat-top-right">
        <span class="iconfont icon-fangda"></span>
        <span class="iconfont icon-zhuanfa"></span>
      </div>
  </div>
  <div class="choose-seat-mark">
      <span class="mark-item">
          <em class="ke check"></em>
          <i>可选</i>
      </span>
      <span class="mark-item">
          <em class="yi check"></em>
          <i>已售</i>
      </span>
      <span class="mark-item">
          <em class="you check"></em>
          <i>最佳观影区</i>
      </span>
  </div>
  <div class="choose-seat-yingmu">
      <div class="yingmu">
          <img src="../assets/img/yingmu.png" alt="">
      </div>
      <i class="ting">{{ting}}号厅银幕</i>
  </div>
</div>

</template>

<script>

export default {
   name:'chooseSeatHeader',
   components: {
       
   },
   props:[""],
   data() {
       return {
           cinema:[],
           ting:"",
       };
   },

   methods: {
    goback() {
        this.$dialog.confirm({
              message:"确定不要刚才选择的座位了吗？",
        }).then(()=>{
            this.$store.state.selectSeat = [];
        if (!this.$store.state.userPath.length) {
                this.$router.push({ path: "/" });
            } else {
                let path = this.$store.state.userPath;
                this.$router.push({ path: path[path.length - 1] });
                this.$store.commit("removePath");
            }
        }).catch(()=>{return})
      
    }
   },
   created(){
       this.ting = this.$store.state.selectMid;
       this.$axios({url:`/cinema/findOne?cid=${this.$store.state.selectCid}`}).then((res)=>{
           console.log(res.data)
           this.cinema = res.data;
       })
   }
}
</script>
<style scoped>
.choose-seat-top{
    padding: 0.1rem 0.12rem;
    color: #2f333c !important;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.choose-seat-top .iconfont,.choose-seat-top-right .iconfont{
    font-size: 0.14rem;
}
.cinema-name{
    font-size: 0.12rem;
}
.choose-seat-top-right .icon-zhuanfa{
    margin-left: 0.1rem;
}
.choose-seat-mark{
    padding: 0.09rem 0;
    display: flex;
    justify-content: center;
}
.mark-item{
  display: flex;
  align-items: center;
  margin-left: 0.1rem;
}
.mark-item .check{
    display: inline-block;
    width: 0.12rem;
    height: 0.12rem;
    background-size: 100% 100%;
}
.ke{
    background: url("../assets/img/kexuan.png");
}
.yi{
    background: url("../assets/img/at6.png");
}
.you{
    background: url("../assets/img/youxuan.png");
}

.mark-item i{
    font-size: 0.07rem;
    margin-left: 0.03rem;
    color: #a2a5aa;
}
.choose-seat-yingmu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.yingmu img{
    width: 1.7rem;
}
.ting{
    font-size: 0.07rem;
    color: #9e9fa4;
    position: relative;
    top: -0.12rem;
}
</style>