<template>
  <div class="_cinemaitem">
      <div id="headbox">
          <span class="iconfont icon-zuojiantou" @click="goback"></span>
          <div>
              <h3>{{flag && cinema.cinemaName}}</h3>
              <p>观影小食&nbsp;|&nbsp;艺术影厅</p>
              <p>{{flag && cinema.address}}&nbsp;></p>
          </div>
      </div>
      <ul>
          <li v-for="(i,index) in movie" :key="index"  @click="focus(index)">
              <img :src="i.src" alt="" :class="{'styleobj1':true,'isfocus':i.isFocus}">
          </li>
      </ul>
      <filmDetails :ting="ting"/>
  </div>
</template>

<script>

import axios from "axios";
import filmDetails from "../components/filmDetails";



export default {
  name: 'CinemaItem',
  components:{
      filmDetails
  },
  data(){
    return {
        ting:0 ,
        cinema:[],
        movie:[
            {src:"",isFocus:false},
            {src:"",isFocus:false},
            {src:"",isFocus:false},
            {src:"",isFocus:false},
            {src:"",isFocus:false},
            {src:"",isFocus:false}         
        ],
        // movie:[],
        cId:"",
        mId:"",
        flag:false,
    }
  },
  methods:{
      goback() {
      if (!this.$store.state.userPath.length) {
        this.$router.push({ path: "/" });
      } else {
        let path = this.$store.state.userPath;
        this.$router.push({ path: path[path.length - 1] });
        this.$store.commit("removePath");

        console.log("用户路径", this.$store.state.userPath);
      }
    },
      focus(i){
          this.$store.commit("updateSelectMid",i+1);
          console.log("换了电影",this.$store.state.selectMid)
          this.movie.forEach((item,index)=>{
              if(i == index){
                //   if(!item.isFocus){

                item.isFocus = true;
                this.ting = i;
                //   }
              }else{
                item.isFocus = false;
              }
          })
      },

  },
  created(){
        this.cId = this.$store.state.selectCid;
        this.mId = this.$store.state.mid;
    axios({
          url:"/cinema/findOne?cid="+this.cId
        }).then(res=>{
          this.cinema= res.data;
          this.flag = true;
          console.log("this.cinema",this.cinema)
    });
    axios({url:"/film/findAll"}).then(res=>{
        res.data.forEach((item,idx)=>{
            this.movie[idx].src = item.cover;
        });
        this.flag = true;
    });
  },
  beforeMount(){
      this.focus(this.$store.state.selectMid-1);
  }
}

</script>
<style scoped>
.styleobj1{width:0.72rem;height:0.96rem;}
.isfocus{width:0.84rem;height: 1.1rem;position: relative;top: -0.15rem}
#headbox{
    position: relative;
    color: white;
    width: 100%;
    height: 1.47rem;
    background-image: url(../assets/img/bg01.png);
    background-size: 100% 100%;
}
#headbox>div{
    overflow: hidden;
    margin: 0 auto;
}
#headbox>div h3{
    margin: 0 auto;
    margin-top: 0.40rem;
    font-size: 0.18rem;
    text-align: center;
}
#headbox>div p:nth-of-type(1){
    font-size: 0.11rem;
    text-align: center;
    margin-top: 0.06rem;
}
#headbox>div p:nth-of-type(2){
    font-size: 0.11rem;
    text-align: center;
    margin-top: 0.08rem;
}
ul{
    display: flex;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    overflow-x: auto;
    position: relative;
    height: 1.2rem;
    padding-top: 0.2rem;
}
ul li{
    margin-right: 0.1rem;
}
ul li img{
    border-radius: 0.05rem;
}
.details{
    width: 100%;
    height: 0.44rem;
}
.details h2{
    font-size: 0.15rem;
    text-align: center;
}
.details h2 span{
    color: #ff7c00;
}
.details p{
    font-size: 0.12rem;
    text-align: center;
    color: #cdcdcd;
    margin-top: 0.1rem;
}






</style>