<template>
  <div class="box">
    <MovieOrderheader />
    <div class="cont">
      <MovieNews :name="film.name" :url="film.cover" />
      <MovieCoupon />
      <CellNumber />
      <div class="bom"></div>
    </div>
    <van-submit-bar
      :price="film.price*selectSeat.length * 100"
      button-text="提交订单"
      @submit="onSubmit"
      text-align="left"
      :loading="isloading"
    />
  </div>
</template>

<script>
import MovieOrderheader from "@/components/MovieOrderheader";
import MovieNews from "@/components/MovieNews";
import MovieCoupon from "@/components/MovieCoupon";
import CellNumber from "@/components/CellNumber";
import axios from "axios";
import { Dialog } from "vant";
// var data = {
//       "cid":this.$store.state.selectCid,
//       "id":this.$store.state.selectMid,
//       "startTime":this.$store.state.startTime,
//       "selectSeat":this.$store.state.selectSeat
//   }
export default {
  name: "Order",
  data() {
    return {
      time: 15 * 60 * 1000,
      isloading: false,
      film: [],
      da: {},
      selectSeat:[]
    };
  },
  components: {
    MovieOrderheader,
    MovieNews,
    MovieCoupon,
    CellNumber,
  },
  methods: {
    onSubmit() {
      this.isloading = true;
      Dialog.confirm({
        title: "确认支付",
        message: "",
      })
        .then(() => {
          // on confirm
          axios({
            method: "post",
            url: "/seat/addseat",
            data: this.da,
          }).then((res) => {
            this.Filmslist = res.data;
            this.isloading = false;
            // Dialog({ 
            //   message: "支付成功",
            //   confirm:function(){
            //     this.$router.push({path:"/pay"})
            //   } 
            // });
            this.$dialog.confirm({
              message:"支付成功",
            }).then(()=>{
              this.$router.push({path:"/pay"});
            })
            // localStorage.setItem("a",1)
            // console.log(this.$route.query.fromPath)
            // this.$router.push({path:this.$route.query.fromPath})
          });
        })
        .catch(() => {
          // on cancel
          Dialog({ message: "支付失败" });
          this.isloading = false;
        });
    },
  },
  created() {
    console.log(JSON.stringify(this.$store.state.selectSeat));
    this.selectSeat=this.$store.state.selectSeat
    this.$axios({
      url: `/film/findById?id=${this.$store.state.selectMid}`,
    }).then((res) => {
      this.film = res.data
      console.log(this.film)
      });
    this.da = {
      "cid": this.$store.state.selectCid,
      "fid": this.$store.state.selectMid,
      // "tid": this.$store.state.startTime,
      "tid": this.$store.state.selectTid,
      // "selectSeat": JSON.stringify(this.$store.state.selectSeat),
      "seat1": this.$store.state.selectSeat,
    };
          console.log("this.da",this.da);
  },
};
</script>
<style scoped>
.cont {
  background-color: #f5f6f8;
}
.bom {
  height: 0.35rem;
}
</style>