<template>
   <div>
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" @change="changeColor">
              <van-swipe-item v-for="(img,index) in imgs" :key="index">
                 <img :src="img" >
              </van-swipe-item>
            </van-swipe>
    </div>
</template>

<script>
// import axios from "axios"

export default {
    name:"Banner",
    data(){
        return {
            imgs:[ "img/banner2.jpg","img/banner1.jpg", "img/banner3.jpg", "img/banner4.jpg", "img/banner5.jpg"]
        }
    },
    methods:{
      changeColor(index){
        this.$emit("changeColor",index);
      }
    }
         
}
</script>

