<template>
  <div class="box">
    <div class="MovieCoupon">
      <h3>本单可享优惠</h3>
      <van-cell is-link @click="showPopup">银行支付优惠 </van-cell>
      <van-popup v-model="show" position="bottom" :style="{ height: '30%' }"
        >无</van-popup
      >
      <van-coupon-cell
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        @click="showList = true"
      />
      <van-popup
        v-model="showList"
        round
        position="bottom"
        style="height: 90%; padding-top: 4px"
      >
        <van-coupon-list
          :coupons="coupons"
          :chosen-coupon="chosenCoupon"
          :disabled-coupons="disabledCoupons"
          @change="onChange"
          @exchange="onExchange"
        />
      </van-popup>
    </div>
  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠12元",
  reason: "",
  value: 150,
  name: "优惠券名称",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "1.5",
  unitDesc: "元",
};

export default {
  data() {
    return {
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon],
      showList:false,
       show: false,
    };
  },
  methods: {
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      console.log(code)
      this.coupons.push(coupon);
    },
    showPopup() {
      this.show = true;
    },
  },
};
</script>
<style scoped>
.box {
  margin: 0 0.09rem;
  margin-bottom: 0.1rem;
  background-color: #f5f6f8;
}
h3 {
  font-size: 0.18rem;
  text-align: left;
  margin: 0 0.15rem;
  height: 0.46rem;
  line-height: 0.45rem;
  border-bottom: 0.01rem solid #f5f5f5;
  width: 100%;
}
.MovieCoupon {
  background-color: #fff;
  border-radius: 0.05rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
</style>