<template>
  <div class="box">
    <van-nav-bar
      title="确认订单"
      :placeholder="isPlaceholder"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
      fixed
    />
    <div class="count-down">
      <van-icon name="underway-o" />
      <van-count-down :time="time" format="mm:ss" />
    </div>
  </div>
</template>

<script>
export default {
  name: "MOvieOrder",
  data() {
    return { time: 15 * 60 * 1000, isPlaceholder: true };
  },
  methods: {
    onClickLeft() {
        // Toast('返回');
      if(!this.$store.state.userPath.length){
                this.$router.push({path:'/'});
            }else{
          let path = this.$store.state.userPath;
          this.$router.push({path:path[path.length-1]});
            this.$store.commit("removePath");
         }
    },
    onClickRight() {
      //   Toast('按钮');
    },
  },
};
</script>
<style scoped>
.box {
  position: relative;
}
.count-down {
  position: fixed;
  display: flex;
  align-items: center;
  right: 0.16rem;
  top: 0.15rem;
  z-index: 1;
}
</style>