<template>
    <div id="app">
         <!-- <Prompt /> -->
         <Search class="header-box"  :style="{backgroundColor:bgc}"/> 
          <div class="center-box">
            <van-tabs v-model="active" :background=bgc class="csc"  sticky offset-top="0.395rem" @click="BgCity" title-inactive-color="white" title-active-color="white" >
              <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
              >
                <van-tab title="精选">  
                          <Banner @changeColor="change" v-if="bannershow"/>
                          <Video />
                          <Wonderfull />
                          <New />
                          <newsFall />
                          </van-tab>
              </van-pull-refresh>
           
            <van-tab title="爱艺之城" >
                <LoveCity />
               <ArtFilm />
                <GoodFilm />
                <FilmFestival />
            </van-tab>
            <van-tab title="大众影评" color="white">
              <PeopleAllDayHot />
              <PeopleEvaluate /> 
              <PersonActive />
             
            </van-tab>
            <van-tab title="演唱会">
              <Vocal />
            </van-tab>
            <van-tab title="最新影讯">
              <Movienew />
            </van-tab>
            <van-tab title="赤狐书生" disabled>内容 4</van-tab>
            <van-tab title="新片看啥" disabled>内容 5</van-tab>
           
            <van-tab title="话剧歌剧" disabled>内容 8</van-tab>
            </van-tabs>
          </div>
         <Bottom class="bottom-box" />
  </div>     

</template>

<script>
// import Prompt from '../components/Prompt.vue'
import Search from '../components/Search.vue'
import Banner from '../components/Banner.vue'
import Video from '../components/Video.vue'
import Bottom from '../components/Bottom.vue'
import Wonderfull from '../components/Wonderfull.vue'
import New from '../components/New.vue'
import Vocal from '@/components/vocal.vue'
import newsFall from '@/components/newsFall.vue'

import PeopleAllDayHot from '../components/PeopleAllDayHot.vue'
import PeopleEvaluate from '../components/PeopleEvaluate.vue'
import PersonActive from  '../components/PersonActive.vue'



import LoveCity from "@/components/LoveCity";
import ArtFilm from "@/components/ArtFilm";
import GoodFilm from "@/components/GoodFilm";
import FilmFestival from "@/components/FilmFestival";


import Movienew from "@/components/Movienew";


import { Toast } from 'vant';
export default {
  name: 'App',
  components: {
    // Prompt,
    Search
    ,Banner,
    Bottom,
    Wonderfull,Video,New, 
    PeopleAllDayHot,
    PeopleEvaluate,
    PersonActive,
    Vocal,
    newsFall,


    ArtFilm,
    GoodFilm,
    FilmFestival,
    LoveCity,

    Movienew

  },
   data() {
    return {
      bannershow:false,
      active: 0,
      bgc:"#db2d60",
      titleActiveColor:"white",
      count: 0,
      isLoading: false,

    };
  },
  beforeMount(){
    this.BgCity(0);
    
  },                                                         
  methods:{
    change(index){
      this.Bannercolor(index);
    },
    Bannercolor(title){
    if(title==0){
        this.bgc="#db2d60"
     }else if(title==1){
        this.bgc="#028489"
     }else if(title==2){
        this.bgc="#b2946c"
     }else if(title==3){
        this.bgc="#112339"
     }else if(title==4){
        this.bgc="#cc4298"
     }
    },
    BgCity(title){
      // console.log(name,title)
     if(title==1){
        this.bgc="#7074bd";
        this.bannershow = false;
     }else if(title==0){
        this.bgc="#db2d62";
        this.bannershow = true;
     }else if(title==2){
        this.bgc="#acd6a6";
        this.bannershow = false;

     }else if(title==3){
        this.bgc=" rgb(209, 129, 219)";
        this.bannershow = false;
     }else if(title==4){
        this.bgc="rgb(113, 182, 199)";
        this.bannershow = false;

     }
    },
      onRefresh() {
      setTimeout(() => {
        Toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
  },
  }

</script>

<style scoped>
/* .csc /deep/ .van-sticky{
  margin-top: -0.006rem;
} */
    #app {
      display: flex;
        flex-direction: column;
        height: 100vh;
        text-align: center;
    }
    .header-box{
      height: 0.4rem;
      background-color: #db2d60;


    }
    .searchColor{
           background-color: #645d5f;
    }
    .center-box{
      flex: 1;
      overflow-y: auto;
    }
      .bottom-box{
      height: 0.4rem;
      position: fixed;
    }
</style>
