import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        selectMid:0,
        selectCid:0,
        selectTid:0,
        midPath:"",
        startTime:"",
        endTime:"",
        selectSeat:[],
        active:0,
        show:false,
        userPath:[]
    },
    mutations:{
        removeAll(state){
            state.userPath = [];
            state.selectSeat = [];
        },
        removePath(state){
            state.userPath.pop();
        },
        updateUserPath(state,newPath){
            state.userPath.push(newPath);
        },


        updateActive(state,newAc){
            state.active = newAc
        },
        updateSelectMid(state,newmid){
            state.selectMid = newmid
        },
        updateSelectCid(state,newcid){
            state.selectCid = newcid
        },
        updateSelectTid(state,newtid){
            state.selectTid = newtid
        },
        updateMidPath(state,newpath){
            state.midPath = newpath;
        },
        updateStartTime(state,newval){
            state.startTime = newval;
        },
        updateEndTime(state,newval){
            state.endTime = newval;
        },
        updateSelectSeat(state,newval){
            // newval.forEach((item) => {
                // let json = {
                //     x:null,
                //     y:null
                // };
                // json.x = item.x;
                // json.y = item.y;
                // state.selectSeat.push(newval);
            // });
            state.selectSeat = newval;
        }
    },
    actions:{

    }
})