<template>
  <div class="">
    <film-detail-top />
    <film-detail-header />
    <film-detail-main/>
  </div>
</template>

<script>
import filmDetailHeader from "../components/filmDetailHeader";
import filmDetailMain from "../components/filmDetailMain";
import filmDetailTop from "../components/filmDetailTop";


export default {
  name: "filmDetail",
  components: {
    filmDetailHeader,filmDetailMain,filmDetailTop
  },
  data() {
    return {
      filmId:1,
      filmData:[],
    };
  },
  created(){
    this.filmId = this.$store.state.selectMid;
    console.log("this.filmId",this.filmId);
    // this.getdata();
  },
  methods:{
    getdata(){
      this.$axios({
        method:"get",
        url:`/film?filmId=${this.filmId}`,
      }).then(res=>{
        this.filmData = res.data;
        // console.log(this.filmData);
    })
  }
}
}
</script>
