<template>
<div class="film-list">
   <div class="box" v-for="film in Filmslist" :key="film.id" @click.self="toFilmDetail(film.id)">
    <div class="film-img" >
      <img :src="film.cover" alt="" />
      <div><van-icon name="play" size="0.12rem" class="film-img-i"/></div>
    </div>
    <div>
      <div class="film-name">
        <h3>{{film.name}}</h3>
        <p><span>3D</span><i>IMAX</i></p>
      </div>
      <div class="film-rating">
        <p>淘票票评分<span>{{film.score}}</span></p>
        <i></i>
        <p>{{film.number}}万人评</p>
      </div>
      <div class="director">导演：{{film.directorname}}</div>
      <div class="to-star">主演：{{film.actor}}</div>
      <div class="film-manifesto">
        <p>{{film.introduction}}</p>
      </div>
    </div>
    <div class="buying-tickets" @click.self="toCinemaList(film.id)">购票</div>
   </div>
</div>
 
</template>

<script>
import axios from "axios";
export default {
  name: "FilmsList",
  data() {
    return {
      Filmslist: [],
      mid:"",
      actor:[],
    };
  },
  created() {

    axios({
      url: "/film/findAll"
      // url: "/film"
    }).then((res) => {
      this.Filmslist = res.data;
      this.mid = this.Filmslist[0].id;
      axios({
        url: `/actor/findAll?id=${this.mid}`
      }).then((res) => {
        this.actor = res.data;
      });
    });
    
  },
  methods:{
    toCinemaList(fimId){
     this.$store.commit("updateActive",0);
      this.$store.commit("updateSelectMid",fimId);
      console.log("当前选中电影id",this.$store.state.selectMid);
      this.$router.push({path:"CinemaList"});
    },
    toFilmDetail(fimId){
     this.$store.commit("updateActive",0);
      this.$store.commit("updateSelectMid",fimId);
      console.log("当前选中电影id",this.$store.state.selectMid);
      this.$router.push({path:"filmDetail"});
    }
  }
};

</script>
<style scoped>
