  position: relative;
  height: 0.72rem;
  margin-right: 0.08rem;
}
.film-img img {
  width: 0.5rem;
  border-radius: 0.05rem;
}

.film-img div {
  width: 0.16rem;
  height: 0.16rem;
  position: absolute;
  border-radius: 50%;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.9;
  text-align: center;
}
.film-img-i{
  position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.film-name {
  display: flex;
  align-items: center;
  margin-top: 0.03rem;
}
.film-name h3 {
  font-size: 0.12rem;
}

.film-name p {
  border: 0.01rem solid #e7e7e8;
  width: 0.36rem;
  height: 0.12rem;
  font-size: 0.06rem;
  color: #9b9fa9;
  display: flex;
  line-height: 0.1rem;
  margin-left: 0.06rem;
  text-align: center;
}

.film-name p span {
  background-color: #9b9fa9;
  color: #fff;
  display: block;
  width: 50%;
  height: 100%;
}

.film-name p i {
  display: block;
  width: 50%;
  height: 100%;
}

.film-rating {
  font-size: 0.1rem;
  color: #636363;
  display: flex;
  align-items: center;
  margin-top: 0.05rem;
}
.film-rating p span {
  color: #fa8817;
  margin-left: 0.02rem;
}
.film-rating i {
  display: block;
  width: 0.02rem;
  height: 0.09rem;
  background-color: #636363;
  opacity: 0.7;
  margin: 0 0.05rem;
}
.to-star,
.director {
  color: #636363;
  font-size: 0.1rem;
  margin-top: 0.05rem;
}

.film-manifesto {
  color: #636363;
  font-size: 0.06rem;
  margin-top: 0.05rem;
}

.film-manifesto p {
  width: 0.58rem;
  height: 0.14rem;
  border: 0.01rem solid #eeeeee;
  text-align: center;
