  white-space: nowrap;
  text-overflow: ellipsis;
}
.buying-tickets{
    width: 0.37rem;
    height: 0.21rem;
    color: #fff;
    text-align: center;
    background-color: #f7367f;
    font-size: 0.1rem;
    position: absolute;
    right: 0.11rem;
    top: 0.3rem;
    height: 0.2rem;
    border-radius: 0.08rem;
    line-height: 0.21rem;
}

.sale{
