<template>
   <div class='app'>
       <div class="chooseSeat-notice">
        <van-swipe style="height: 0.28rem;line-height:0.28rem" :touchable="false" :autoplay="3000" vertical :show-indicators='false'>
        <van-swipe-item>
            <div class="notice">
                <div class="notice-left">
                    <img src="../assets/img/laba.webp" alt="">
                    <i>支持支付宝实名认证用户购票</i>
                </div>
                <span class="iconfont icon-youjiantou"></span>
            </div>
        </van-swipe-item>
        <van-swipe-item>
            <div class="notice">
                <div class="notice-left">
                    <img src="../assets/img/laba.webp" alt="">
                    <i>疫情期间，观影需全程佩戴口罩，影厅内禁止饮食</i>
                </div>
                <span class="iconfont icon-youjiantou"></span>
            </div>
        </van-swipe-item>
        </van-swipe>
       </div>
       <div class="chooseSeat-movie">
           <div class="movie-name">
               <i>{{filmData.name}}</i>
           </div>
           <div class="movie-info">
               <p>今天 {{movie.date}} {{$store.state.startTime}}-{{$store.state.endTime}} 国语2D</p>
           </div>
       </div>
       <div class="chooseSeat-btn">
            <ul>
                <li class="seat-info" v-for="(item,index) in priSeat" :key="index" @click="dispose(index)">
                    <p>{{item.x}}排{{item.y}}座</p>
                    <i>￥</i><span>{{moeny}}</span>
                    <em>×</em>
                </li>
            </ul>
            <div class="seat-btn fw">
                <div class="btn-text nocheck" :class="{haschecked:haschecked}" @click="toOrder">
                    <i>{{totals}}</i>
                    <em>{{text}}</em>
                </div>
            </div>
        </div>
   </div>
</template>

<script>
import { Swipe, SwipeItem } from "vant";
export default {
   name:'chooseSeatBottom',
   components: {
       [Swipe.name]:Swipe,
       [SwipeItem.name]:SwipeItem,
   },
   props:["priSeat"],
   data() {
       return {
           filmData:[],
           text:"请先选座",
           totals:"",
           moeny: 0,
           haschecked:false,
           movie:{
               name:"疯狂原始人2",
               date:"",
               time:"17:00-18:36",
           },
       };
   },
   methods: {
       getDate(){
           let date = new Date();
           this.movie.date = `${date.getMonth()+1}月${date.getDate()}日`;
       },
       dispose(index){
            this.priSeat.splice(index,1);
            this.$emit("remPri",this.priSeat);
       },
       //小计
        total(){
            let num = this.priSeat.length * this.moeny;
            if(num==0){
             this.totals = "";
             this.text = "请先选座";
             this.haschecked = false;
            }else{
                this.totals = num + "元";
                this.text = "确认选座";
                this.haschecked = true;
            }
            
        },
        toOrder(){
            this.$emit("toOrder");
            if(this.haschecked){
                this.$router.push({path:"/OrderPage"})
            }
        }
   },
   beforeMount(){
       this.getDate();
   },
   created(){
       this.$axios({url:`/film/findById?id=${this.$store.state.selectMid}`}).then(res=>{
           this.filmData = res.data;
           this.moeny = this.filmData.price;
       })
   },
   watch:{
       priSeat:{
           deep:true,
           handler:function(){
               this.total();
           }
       }
   }
}
</script>
<style scoped>
.app{
    width:  100%;
    padding: 0 0.06rem;
    position: fixed;
    bottom: 0;
}
.chooseSeat-notice,.chooseSeat-movie{
    background: #fff;
    padding: 0 0.1rem;
    border-radius: 0.05rem;
    box-shadow: 0 0 0.06rem 0 rgba(0,0,0,.06);
    margin-bottom: 0.03rem;
}
.notice{
    font-size: 0.09rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.notice img{
    width: 0.1152rem;
    margin-right: 0.04rem;
    display: inline-block;
}
.notice-left{
    color: #666;
}
.notice .iconfont{
    font-size: 0.07rem;
}
.chooseSeat-movie{
    padding: 0.08rem 0.1rem 0.06rem;
}
.movie-name{
    font-size: 0.1rem;
    color: #2f333c;
    margin-bottom: 0.03rem;
}

.movie-info p{
    font-size: 0.08rem;
    color: #666;
}
.chooseSeat-btn{
    padding: 0.04rem 0.06rem;
    background: #fff;
    border-radius: 0.05rem;
    box-shadow: 0 0 0.06rem 0 rgba(0,0,0,.06);
}
.chooseSeat-btn ul{
    display: flex;
    overflow-x: auto;
    padding: 0 0.1rem 0.08rem;
}
.seat-info{
    flex-shrink: 0;
    background: #f5f5f5;
    display: inline-block;
    padding: 0.03rem;
    width: 0.7rem;
    border-radius: 0.05rem;
    margin-right: 0.08rem;
}
.seat-info em{
    font-size: 0.1rem;
    color: #666;
    position: relative;
    right: -0.35rem;
    top: -0.1rem;
}
.seat-info p{
    font-size: 0.1rem;
}
.seat-info i{
    color: #ff435b;
    font-size: 0.08rem;
}
.seat-info span{
    color: #ff435b;
    font-size: 0.12rem;
}
.seat-btn{
    width: 100%;
    height: 0.33rem;
    background-image: linear-gradient(45deg,#ff5456,#ff3d5c);
    border-radius: 0.3rem;
    text-align: center;
    line-height: 0.33rem;
    letter-spacing: 0.01rem;
}
.btn-text i{
    margin-right: 0.2rem;
}
.nocheck{
    font-size: 11px;
    color: rgba(196, 196, 196, .5);
}
.haschecked{
    color: #fff;
}
</style>