<template>
   <div class="box">
       <div class="details">
          <h2>{{show && film[ting].name}}<span> 淘票票评分 {{show &&  film[ting].score}}</span></h2>
          <p>{{show &&  film[ting].duration}}分钟&nbsp;|&nbsp;{{show &&  film[ting].type}}&nbsp;|&nbsp;{{show &&  film[ting].actor}}</p>
       </div>
       <van-tabs v-model="active">
        <van-tab :title=today>
            <scheduleList :filmId="film[ting].id" :timelong="duration" :price="film[ting].price" v-if="show"></scheduleList>
        </van-tab>
        <van-tab :title=tomorrow ></van-tab>
      </van-tabs>
   </div>

    
</template>
<script>
import axios from "axios"
import scheduleList from '../components/schedule-list'

export default {
    name:"filmDetails",
    components:{
      scheduleList
  },
  props:["ting"],
  data() {
    return {
        flag:"",
        active:0,
        today:"",
        tomorrow:"",
        film:[],
        now:null,
        duration:[],
        show:false
    };
  },
  created(){
      var date = new Date();
      var m = date.getMonth() +1;
      var d = date.getDate()<10?"0"+date.getDate():date.getDate();
      this.today = "今天"+m+"-"+d;
      this.tomorrow = "明天"+m+"-"+(Number(d)+1);
      axios({
          url:"/film/findAll"
      }).then(res=>{
          this.film = res.data;
          this.show = true;
          this.film.forEach(item=>{
              this.duration.push(item.duration)
          }) 
      })
  },
};
</script>

<style scoped>
.details{
    width: 100%;
    height: 0.44rem;
}
.details h2{
    font-size: 0.15rem;
    text-align: center;
}
.details h2 span{
    color: #ff7c00;
}
.details p{
    font-size: 0.12rem;
    text-align: center;
    color: #cdcdcd;
    margin-top: 0.1rem;
}

</style>