<template>
  <div class="app">
    <div class="heade">
      <img src="../assets/img/l1.png" class="heade-top" @click="goback" />
      <img src="../assets/img/q1.png" class="heade-bottom" />
    </div>
    <div class="conten">
      <div class="conten-top">
        <input
          type="text"
          style="border: 0px"
          placeholder="手机号/邮箱/会员名"
          v-model="userTel"
        />
      </div>
      <div class="conten-bottom">
        <input
          type="text"
          style="border: 0px"
          placeholder="请输入登录密码"
          v-model="userPwd"
        />
        <img src="../assets/img/w1.png" />
      </div>
    </div>
    <div class="fant">
      <p>短信验证码登录</p>
      <router-link to="/Singer"><p>免费注册</p></router-link>
    </div>
    <div class="foote">
      <input
        type="button"
        style="color: #ffffff"
        value="登陆"
        @click="regSave()"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Toast } from "vant";

export default {
  name: "login",
  data: function () {
    return {
      userTel: "",
      userPwd: "",
    };
  },
  components: {
    [Toast.name]: Toast,
  },
  created() {
    if (sessionStorage.getItem("Newuser")) {
      this.userTel = sessionStorage.getItem("Newuser");
    }
  },
  methods: {
    goback() {
      if (!this.$store.state.userPath.length) {
        this.$router.push({ path: "/" });
      } else {
        let path = this.$store.state.userPath;
        this.$router.push({ path: path[path.length - 1] });
        this.$store.commit("removePath");
      }
    },
    regSave() {
      // 1、非空判断，正则验证
      if (this.userTel == "") {
        Toast("电话不能为空");
        return;
      }
      if (this.userPwd == "") {
        Toast("密码不能为空");
        return;
      }
      let telReg = /^1\d{10}$/; //以1开头，后面有十位数字
      if (!telReg.test(this.userTel)) {
        Toast("电话格式不正确");
        return;
      }

      // 2、发送请求
      this.isLoading = true;
      let data = new URLSearchParams();
      data.append("userTel", this.userTel);
      data.append("userPwd", this.userPwd);

      axios({
        url: `/user/login?userTel=${this.userTel}&userPwd=${this.userPwd}`,
        method: "get",
        // data:`username=${this.username}&userpass=${this.userpass}`
        //   data
        // data:{
        //   username:this.username,
        //   userpass:this.userpass
        // }
      }).then((res) => {
        this.isLoading = false;
        //   console.log(res.data);
        //   if(res.data){
        // 	localStorage.setItem("a",1)
        // 	alert("登陆成功！");
        // 	this.$router.push({path:this.$route.query.fromPath});
        //   }else{
        // 	alert("登陆失败！");
        if (res.data == "该用户未注册") {
          Toast(res.data);
        } else if (res.data == "密码错误") {
          Toast(res.data);
        } else {
          Toast(res.data);
          localStorage.setItem("userTel", this.userTel);
          // this.$router.push({path:this.$store.state.midPath});
          if (!this.$store.state.userPath.length) {
            this.$router.push({ path: "/" });
          } else {
            let path = this.$store.state.userPath;
            this.$router.push({ path: path[path.length - 1] });
            this.$store.commit("removePath");
          }
        }
      });
    },
  },
};
</script>

<style scoped>
.app .heade {
  height: 132px;
  /* background-color: aqua; */
  position: relative;
}
.app .heade .heade-top {
  position: absolute;
  height: 45px;
  width: 45px;
  top: 10px;
  left: 10px;
}
.app .heade .heade-bottom {
  position: absolute;
  top: 50px;
  left: 137px;
  border-radius: 50%;
}
.conten .conten-top {
  text-decoration: underline;
  border-bottom: 1px solid #ff7606;
  width: 320px;
  height: 34px;
  margin: 0 auto;
  margin-top: 42px;
}
.conten .conten-bottom {
  text-decoration: underline;
  border-bottom: 1px solid #ff7606;
  width: 320px;
  height: 34px;
  margin: 0 auto;
  margin-top: 70px;
  position: relative;
}
.conten .conten-bottom img {
  position: absolute;
  right: 10px;
}
.fant {
  width: 320px;
  display: flex;
  justify-content: space-between;
  margin: 10px auto;
}
.foote {
  width: 320px;
  margin: 0 auto;
}
.foote input {
  height: 46px;
  width: 100%;
  border-radius: 10px;
  margin-top: 30px;
  background-color: #ff7c00;
  margin-right: 10px;
}
</style>
