<template>
    <div id="pay">
            <van-nav-bar
                    title="今天开场"
                    left-text=""
                    :left-arrow="true"
                    @click-left="onClickLeft"
                class="payTop" />
      
        <div class="payContent">
            凭二维码取票更方便，取票码将不再以短信形式发送
        </div>
        <ul class="getOut">
            <li>
                <p>取票</p>
                <p>如何取票</p>
            </li>
            <li>
                <div class="erweima" ref="mama"></div>
            </li>
            <li>取票号 {{number}}</li>
            <li>
                <div class="phone">
                    <span class="icontfont icon-shouji"></span>
                    <p>保存到手机</p>
                </div>
                  <div class="enjoy">
                    <span class="iconfont icon-haoyou"></span>
                    <p>分享给好友</p>
                </div>
                 
            </li>
        </ul>
    </div>
</template>

<script>
import Vue from 'vue';
import { NavBar } from 'vant';
import QRCode from 'qrcodejs2'

Vue.use(NavBar);

import { Toast } from 'vant';

export default {
    data(){
      return {
          film:"",
          cinema:"",
          number:""
      }  
    },
  methods: {
    onClickLeft() {
      this.$store.commit("removeAll");
      this.$router.push({path:"/Home"});
    },
    qrcode(){
        this.$axios.all([this.$axios({url:`/film/findById?id=${this.$store.state.selectMid}`}),this.$axios({url:`/cinema/findOne?cid=${this.$store.state.selectCid}`})]).then(
            this.$axios.spread((film,cinema)=>{
                let str = "";                   
                this.film = film.data.name;
                this.cinema = cinema.data.cinemaName;
                this.$store.state.selectSeat.forEach(item => {
                    str += item.x + "排" + item.y + "座" + " ";
                });
        let qr = new QRCode(this.$refs.mama,{
            text: `
电影：${this.film}
电影院：${this.cinema}
座位号：${str}
电影开始时间：${this.$store.state.startTime}
电影结束时间：${this.$store.state.endTime}

`,
            width: 200,
            height: 200,
            colorDark: "#333333", //二维码颜色
            colorLight: "#ffffff", //二维码背景色
            correctLevel: QRCode.CorrectLevel.L //容错率，L/M/H
        });





            })
        )




    },
    getNum(){
        for(let i = 0;i<16;i++){
            this.number +=  Math.round(Math.random() * 9);
        }
    }
  },
  mounted(){
      this.getNum();
      this.$nextTick(this.qrcode);
  }

};

</script>


<style scoped>
@import url('../assets/font3/iconfont.css');
#pay{
    background-color: #cbab70;
    width: 100%;
    height: 900px;
    
   
}
#pay /deep/ .van-icon.van-icon-arrow-left.van-nav-bar__arrow{
  color: white;

}
#pay /deep/ .payTop{
  background-color: #cbab70;
}

#pay /deep/ .van-nav-bar__title{
  color: white;

}
.payContent{
     color: #f5e7d2;
     font-size: 11px;
     margin-top: 20px;
     text-align: center;
}
.getOut{
    width: 340px;
    height: 510px;
    background-color: white;
    margin: auto;
    border-radius: 10px;
}
.getOut li:nth-of-type(1){
    width: 340px;
    height: 67px;
   display: flex;
   margin-top: 20px;
   padding-top: 20px;
   padding-left: 10px;
    padding-right: 10px;
   justify-content: space-between;
}
.getOut li:nth-of-type(1) p:nth-of-type(2){
    color: #79bce7;
}
.getOut li:nth-of-type(2) .erweima{
   margin: auto;
   margin-top: 20px;
}
.getOut li:nth-of-type(2) p{
    text-align: center;
    margin-top: 20px;
    font-size: 12px;
    color: #92969a;
}
.getOut li:nth-of-type(3){
    width: 286px;
    height: 43px;
    border: 2px solid #f5f5f5;
    border-radius: 10px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 40px;
    text-align: center;
    line-height:43px;
}
.getOut li:nth-of-type(4){
   width: 295px;
   height: 74px;
 display: flex;  
   margin: auto;
   justify-content: space-around;
   border-top:1px solid #f5f5f5;
    border-bottom:1px solid #f5f5f5;
}
.phone{
    border-right: 1px solid #f5f5f5;
}
.phone,.enjoy{
    width: 147px;
    text-align: center;
    padding-top: 10px;
    box-sizing: border-box;
}
.phone span, .enjoy span{
    font-size: 20px;
    display: block;
    padding-bottom: 10px;

}
.phone p, .enjoy p{
    font-size: 12px;
    color: #91909a;
}
.erweima{
    width: 2rem;
}
</style>