<template>
	<div>
		<div class="header">
			<img src="../assets/img/l1.png" class="header-top" @click="tologin">
		</div>
		<div class="content">
			<div class="content-top">
			<input type="text" style="border: 0px;" placeholder="请输入手机号码" v-model="userTel">
			</div>
			<div class="content-bottom">
			<input type="text" style="border: 0px;" placeholder="请输入验证码" v-model="userPwd">
			<p>获取验证码</p>
			</div>
		</div>
		<div class="footer">
			<input  type="button"
				style="color: #ffffff;"
				value="注册"
				@click="regSave()"
			/>
		</div>
		
	</div>
</template>

<script>
	import axios  from "axios";
	import { Toast } from 'vant';
	export default{
		name:"Singer",
		data:function(){
			return{
				userTel:"",
				userPwd:"",
			}
		},
		components:{
			[Toast.name]:Toast
		},
		methods: {
		  tologin(){
			   if (!this.$store.state.userPath.length) {
        this.$router.push({ path: "/" });
      } else {
        let path = this.$store.state.userPath;
        this.$router.push({ path: path[path.length - 1] });
		this.$store.commit("removePath");
	  }
		  },
		  regSave() {
		    // 1、非空判断，正则验证
		  			if(this.userTel==""){
						  Toast("电话不能为空")
		  				return;
		  			}
		  			if(this.userPwd==""){
		  				  Toast("密码不能为空");
		  				return;
		  			}
		  			let telReg = /^1\d{10}$/; //以1开头，后面有十位数字
		  			if(!telReg.test(this.userTel)){				
		  				  Toast("电话格式不正确");
		  				return;
		  			}
		  			
		  		
		    // 2、发送请求
			this.isLoading = true;
		    let data = new URLSearchParams();
		    data.append("userTel", this.userTel);
		    data.append("userPwd", this.userPwd);
		  		
		    axios({
			//   url: "http://192.168.43.13/user/login?usetTel=123&userPwd=test",
			  url:`/user/regist?userTel=${this.userTel}&userPwd=${this.userPwd}`,
		      method: "post",
		      // data:`username=${this.username}&userpass=${this.userpass}`
		    //   data
		      // data:{
		      //   username:this.username,
		      //   userpass:this.userpass
		      // }
		    }).then(res => {
		      this.isLoading = false;
				if(res.data == "注册成功"){
					Toast(res.data);
					sessionStorage.setItem("Newuser",this.userTel);
					this.$router.push({path:"/login"});
				}else{
					Toast(res.data);
				}
		    });
		  }
		}
	}
</script>

<style scoped>
	.header{
		height: 132px;
		/* background-color: aqua; */
		position: relative;
	} 
	.header-top{
		position: absolute;
		height: 45px;
		width: 45px;
		top: 10px;
		left: 10px;
	}
	.content-top{
		text-decoration:underline;
		border-bottom: 1px solid #ff7606;
		width: 320px;
		height: 34px;
		margin: 0 auto;
		margin-top: 22px;
		margin-left: 28px;
		background-color: #ffffff;
	}
	.content-bottom{
		text-decoration:underline;
		border-bottom: 1px solid #ff7606;
		width: 320px;
		height: 34px;
		margin: 0 auto;
		margin-top: 70px;
		position: relative;
	}
	.content .content-bottom p{
		position: absolute;
		right: 10px;
		margin-top: -15px;
		/* margin: 0 auto; */
	}
	.footer{
		width: 320px;
		margin: 0 auto;
	}
	.footer input{
		height: 46px;
		border-radius: 10px;
		margin: 0 auto;
		margin-top: 30px;
		background-color: #FF7c00;
		left: 20px;
		width: 320px;
		box-sizing: border-box;
	}
</style>
