<template>
  <div class="box">
    <div class="movie-news">
      <div class="movie-img"><img :src="url" alt="" /></div>
      <div class="movie-text">
        <h3>{{name}}</h3>
        <p>今天{{date}} {{$store.state.startTime}} - {{$store.state.endTime}} (国语2D)</p>
        <p>{{cinema.cinemaName}}</p>
        <p>{{$store.state.selectMid}}号厅 <i v-for="(item,index) in selectSeat" :key="index"> {{item.x}}排{{item.y}}列</i></p>
      </div>
    </div>
      <hr />
    <div class="movie-price">
      <div>
        <van-icon name="passed" color="#1ebb99" />支持退票
        <van-icon name="passed" color="#1ebb99" />支持改签
        <van-icon name="arrow" />
      </div>
      <div><p>(共{{selectSeat.length}}张) 原价￥{{selectSeat.length*50}}</p></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MOvieOrder",
  data() {
    return { time: 15 * 60 * 1000, isPlaceholder: true ,selectSeat:[],date:"",cinema:[]};
  },
  props:["name","url"],
  created(){
    this.selectSeat=this.$store.state.selectSeat
    let d = new Date();
    this.date=d.getMonth()+1+"-"+d.getDate()
    this.$axios({
      url:`/cinema/findOne?cid=${this.$store.state.selectCid}`,  
    }).then(res=>(this.cinema=res.data))
  }
};
</script>
<style scoped>
.box {
  text-align: left;
  padding: 0.13rem 0.09rem 0.1rem;
}
.movie-img {
  margin-right: 0.12rem;
}
.movie-news {
  background-color: #fff;
}
.movie-img img {
  width: 0.8rem;
  height: 1.15rem;
}
.movie-news {
  display: flex;
  padding: 0.15rem 0.2rem 0.08rem;
  justify-content: space-between;
  border-radius: 0 0 0.15rem 0.15rem;
}

.movie-news h3 {
  font-size: 0.18rem;
  margin: 0.08rem 0;
}
.movie-news p {
  font-size: 0.12rem;
  line-height: 0.15rem;
  margin-top: 0.08rem;
}
.movie-price {
  display: flex;
  align-items: center;
  font-size: 0.12rem;
  background-color: #fff;
  height: 0.45rem;
  justify-content: space-between;
  padding: 0 0.1rem;
  border-radius: 0.15rem 0.15rem 0 0;
}
hr{
  width: 3.1rem;
  margin: 0 auto;
  border-color: #f4f4f4;
}
</style>