<script>
import axios from "axios";
var d = new Date();
export default {
  props:["filmId","timelong","price"],
  data() {
    return {
      film:[],
      startTime:["9:00","12:00","15:30","18:00","21:00"],
    //   start:[9,12,15,18,21],
      endTime:[[],[],[],[],[],[]],
    };
  },
  created(){
      this.timelong.forEach((long,idx)=>{
          this.startTime.forEach(start=>{
              this.endTime[idx].push(this.Endtime(start,long));
          })
      });
  },
  methods:{
      Endtime(startTime,timelong){
        let h = parseInt(timelong/60);
        let min = timelong%60;
        let arr=startTime.split(":");
        let H = Number(arr[0])+h;
        let M =0;
        if(Number(arr[1])+min>60){
            H++;
            M=Number(arr[1])+min-60;
        }else{
            M=Number(arr[1])+min;
        }
        if(M<10){
            M="0"+M;
        }
        return H+":"+M;
        // this.$store.commit("updateEndTime",this.endTime);
        // return this.endTime;
      },
      overtime(){
          let d = new Date();
          return d.getHours();
      },
    toChooseSeat(idx){
        this.$store.commit("updateStartTime",this.startTime[idx-1]);
        this.$store.commit("updateSelectTid",idx);
        this.$store.commit("updateEndTime",this.endTime[this.filmId-1][idx-1]);
        this.$router.push({path:"/chooseSeat"})
    }
},
};
</script>

