<template>
  <div class="_cinemalist">
      <div id="Chead">
         <span class="iconfont icon-zuojiantou" @click="goback"></span>
         <span class="iconfont icon-sousuo"></span>
         <span class="iconfont icon-koubei"></span>
      </div>
      <cinemalist-header h="38px"></cinemalist-header>
      <ul>
          <li v-for="(item,index) in cinema" :key="index" @click="toCinemaItem(index)">
              <h3>{{item.cinemaName}}<span>{{item.cost}}<b>&nbsp;起</b></span></h3>
              <p>好评度{{item.evaluate}}&nbsp;|&nbsp;{{item.address}}<b>{{item.distance}}</b></p>
              <p id="tag"><span>影城卡特惠</span><span>特惠票</span><span>可退票</span><span>可改签</span><span>观影小食</span><span>可停车</span></p>
              <div class="timebox">
                 <p>
                    <span>9:00</span>
                    <span>国语2D</span>
                    <span>￥23.9</span>
                 </p>
                 <p>
                    <span>12:00</span>
                    <span>国语2D</span>
                    <span>￥23.9</span>
                 </p>
                 <p>
                    <span>15:30</span>
                    <span>国语2D</span>
                    <span>￥23.9</span>
                 </p>
              </div>
          </li>
      </ul>
  </div>
</template>

<script>
import cinemalistHeader from '../components/cinemalist-header'
import axios from "axios";


export default {
  name: 'CinemaList',
  components:{
    cinemalistHeader
  },
  data(){
    return {
      cinema:[],
      filmId:"",
      cId:""
    }
  },
  methods:{
    toCinemaItem(idx){
      this.$store.commit("updateSelectCid",idx+1);
      this.$router.push({path:"CinemaItem"})
    },
    goback(){
      if (!this.$store.state.userPath.length) {
        this.$router.push({ path: "/" });
      } else {
        let path = this.$store.state.userPath;
        this.$router.push({ path: path[path.length - 1] });
        this.$store.commit("removePath");
      } 
    }
  },
  created(){
    axios({
      url:"/cinema/findAll",
      // url:"/cinemalist",
      
      method:'get',
      params:{
      },
    })
    .then(res=>{
      this.cinema = res.data;
    })
  }
}

</script>

<style scoped>
._cinemalist{
  background-color: #f5f6f8;
  width: 100%;
}
ul{
  width: 100%;
  /* margin: 0 auto; */
  margin-top: 0.1rem;
}
li{
  background-color: white;
  width: 95%;
  height: 1.35rem;
  border-radius: 0.1rem;
  position: relative;
  margin: 0 auto;
  margin-bottom: 0.07rem;
}
li h3{
  font-size: 0.12rem;
  position: absolute;
  width: 92%;
  left: 0.11rem;
  top: 0.17rem;
}
li h3 span{
  float: right;
  color: #fa4967;
  font-size: 0.09rem;
}
li h3 span b{
  color: #c0c3ca;
}
li>p{
  font-size: 0.08rem;
  position: absolute;
  left: 0.11rem;
  top: 0.38rem;
  color: #8e8d93;
  width: 90%;
}
#tag{
  left: 0.11rem;
  top: 0.56rem;
}
#tag>span{
  font-size: 0.06rem;
  padding: 0.02rem 0.03rem 0.02rem 0.03rem;
  border-radius: 0.02rem;
  margin-right: 0.01rem;
}
#tag>span:nth-of-type(1){
  color: #50b6a0;
  border: 0.01rem solid #50b6a0;
}
#tag>span:nth-of-type(2){
  color: #e7a142;
  border: 0.01rem solid #e7a142;
}
#tag>span:nth-of-type(3){
  color: #6bb7e8;
  border: 0.01rem solid #6bb7e8;
}
#tag>span:nth-of-type(4){
  color: #6bb7e8;
  border: 0.01rem solid #6bb7e8;
}
#tag>span:nth-of-type(5){
  color: #61656e;
  border: 0.01rem solid #61656e;
}
#tag>span:nth-of-type(6){
  color: #61656e;
  border: 0.01rem solid #61656e;
}

li p:nth-of-type(1) b{
  float: right;
  font-weight: normal;
}
.timebox{
  position: absolute;
  left: 0.11rem;
  top: 0.8rem;
}
.timebox>p{
  width: 0.7rem;
  height: 0.5rem;
  background-color: #f5f5fd;
  float: left;
  margin-right: 0.06rem;
